import http from '@/api/http.js'
import QSPopup from '@/components/QS-popup/QS-popup.vue';
const app = getApp();
export default {
	components:{QSPopup},
	data() {
		return {
			imag: app.globalData.imag,
			isEmpty:true,
			selected:false,
			totalSelect:false,
			selectImg:'/static/select.png',
			unSelectImg:'/static/unselect.png',
			setStatus:true,  //管理
			goodsList:[],	//购物车列表
			recomList:[],	//推荐商品列表
			totalMoney:0, // 商品总价
			page:1,	//分页
			// types:0//0是普通商品  1是会员商品  显示在简介后
			// selectArr : [],//购物车选中缓存
		}
	},
	onLoad(){
		this.getGoodsList()
	},
	onShow() {
		this.getGoodsList()
		
		this.isLogon()
	},
	onHide() {
		this.airBrake()
	},
	onReachBottom() {
		this.getNewList()
	},
	methods:{
		toIndex(){
			uni.switchTab({
				url:'/pages/index/index'
			})
			this.hides('QSPopuplogin')
		},
		toLogin(){
			uni.navigateTo({
				url:'/pages/index/logon/logon'
			})
			this.hides('QSPopuplogin')
		},
		isLogon(){
			if(!uni.getStorageSync('token')){
				this.show('QSPopuplogin')
			}
			if(uni.getStorageSync('token')){
				let data = {
					token:uni.getStorageSync('token')
				}
				http('/api/User/get_userinfo','get',data).then(res=>{
					if(res.data.status == '200'){
						
					}else{
						this.$util.showToast('登录信息已过期，请重新登录')
						setTimeout(()=>{
							uni.navigateTo({
								url:'/pages/index/logon/logon'
							})
						},1500)
					}
					console.log(res)
				})
			}
		},
		show(ref) {
			this.$refs[ref].show();
		},
		hides(ref) {
			this.$refs[ref].hide();
		},
		delGoods(){
			this.hides('QSPopup')
			let cartids = [];
			
			this.goodsList.map(item=>{
				if(item.check){
					cartids.push(item.id)
				}
			})
			if(cartids.length==0){
				return false;
			}
			let data = {
				token:uni.getStorageSync('token'),
				car_id:cartids.join(',')
			}
			http('/api/Car/delUserCar','post',data).then(res=>{
				
				this.getGoodsList()
			})
			this.totalSelect = false
			this.totalMoney = 0
		},
		//购物车选中
		selectArrCar(){
			let that = this
			let selectArr = that.$util.getStorage('selectArr')
			that.goodsList.forEach((item)=>{
				selectArr.forEach((select)=>{
					if(item.id==select){
						item.check = true
					}
				})
			})
		},
		selectedTap(item){
			let _this = this;
			// 记录选中的商品种类个数
			let num = 0;
			let selectArr = [];//购物车选中缓存
			
			// 注意对比前后两次输出的结果
			console.log(item.check);
			if (typeof item.check == "undefined") {
				_this.$set(item, "check", false);
				// 注意对比前后两次输出的结果
				console.log(item.check);
			} else {
				item.check = !item.check;
				_this.goodsList.forEach(function (item, value) {
					_this.$util.setStorage('selectArr',[])
					if (item.check) {
						selectArr.push(item.id)
						_this.$util.setStorage('selectArr',selectArr)
					}
				})
			}
			_this.goodsList.forEach(function (item, value) {
				if (item.check) {
					num++;
					selectArr.push(item.id)
					_this.$util.setStorage('selectArr',selectArr)
				}
			})
			console.log(_this.$util.getStorage('selectArr'))
			// 如果选中的商品种类个数等于商品列表（数组）的长度，修改全选框的全选标志；
			if (num == _this.goodsList.length) {
				_this.totalSelect = true;
			} else {
				_this.totalSelect = false;
			}
			// 计算总价
			
			_this.getTotalMoney();
			
		},
		airBrake(){   //  置空
			let _this = this;
			_this.setStatus = true
		},
		totalSelectedTap(){
			let selectArr = [];//购物车选中缓存
			let _this = this;
			_this.totalSelect = !_this.totalSelect;
			
			_this.goodsList.forEach(function (item, index) {
				if (typeof item.check == "undefined") {
					// 设置item的check属性
					_this.$set(item, "check", _this.totalSelect);
					_this.goodsList.forEach(function (item, value) {
						_this.$util.setStorage('selectArr',[])
						if (item.check) {
							selectArr.push(item.id)
							_this.$util.setStorage('selectArr',selectArr)
						}
					})
				} else {
					item.check = _this.totalSelect;
					_this.goodsList.forEach(function (item, value) {
						_this.$util.setStorage('selectArr',[])
						if (item.check) {
							selectArr.push(item.id)
							_this.$util.setStorage('selectArr',selectArr)
						}
					})
				}
			})
			// 计算总价
			_this.getTotalMoney()
		},
		setStatusTap(){
			this.setStatus = !this.setStatus
		},
		//获取列表
		getGoodsList(){
			let data = {
				token:uni.getStorageSync('token'),
				page:1
			}
			http('/api/Car/userCarList','post',data).then(res=>{
				console.log(res)
				// this.isLogon()
				this.recomList = res.data.data.car_goods_list
				if(res.data.data.userCarList.data.length == 0){
					this.isEmpty = true
				}else if(res.data.data.userCarList.data.length > 0){
					this.goodsList = res.data.data.userCarList.data
					
					this.isEmpty = false;
				    this.goodsList.map((item)=>{
						this.$set(item,'check',false)
					})
					this.selectArrCar()
					this.getTotalMoney()
				}
			})
		},
		getNewList(){
			let data = {
				token:uni.getStorageSync('token'),
				page:this.page+1
			}
			http('/api/Car/userCarList','post',data).then(res=>{
				
				if(res.data.status=='400'){
					this.$util.showToast(res.data.msg)
					return
				}
				
				if(this.page > res.data.data.userCarList.last_page){
					// this.$util.showToast('没有更多了')
					return false
				}
				this.page++
				this.goodsList = this.goodsList.concat(res.data.data.userCarList.data)
				this.goodsList.map((item)=>{
					this.$set(item,'check',false)
				})
				this.selectArrCar()
				this.getTotalMoney()
			})
		},
		//总金额计算 
		getTotalMoney() {
			let _this = this;
			let total = 0
			// 商品总价首先置0，判断选中后，再计入总价
			_this.totalMoney = 0;
			_this.goodsList.forEach(function (item, index) {
				// 如果商品被选中，则计算到总价之中，运算符使用+=
				if (item.check) {
					total += (((parseFloat(item.goods_price)*100) * item.num)/100)
					_this.totalMoney = total.toFixed(2)
					// _this.totalMoney += (parseFloat(item.totalprice)*100)/100
				}
			})
		},
		toPay(){
			let cartid =[];
			this.goodsList.map((item)=>{
				if(item.check){
					cartid.push(item.id)
				}
			})
			if(cartid.length==0){
				return false;
			}
			console.log(111111);
			
			this.$util.setStorage('orderdata',{
				token:uni.getStorageSync('token'),
				type:2,
				cart_id:cartid.join(',')
			})
			uni.navigateTo({
				url:'/pages/index/pay/pay?type=2&cart_id='+cartid.join(',')
			})
			this.totalMoney = 0
			this.totalSelect = false
			this.setStatus = true
		},
		addNum(item){
			if(item.num >= 1){      //定义一个 初始价格
				
				let data = {
					token:uni.getStorageSync('token'),
					car_id:item.id,
					type:1   //  1 添加  2  减少
				}
				http('/api/car/saveCar','post',data).then(res=>{
					console.log(res)
					this.getGoodsList()
					this.getTotalMoney()
					// this.totalMoney = 0
					// this.totalSelect = false
				})
			}
			
		},
		jianNum(item){
			if(item.num > 1){      //定义一个 初始价格
				let data = {
					token:uni.getStorageSync('token'),
					car_id:item.id,
					type:2   //  1 添加  2  减少
				}
				http('/api/car/saveCar','post',data).then(res=>{
					console.log(res)
					this.getGoodsList()
					this.getTotalMoney()
					// this.totalMoney = 0
					// this.totalSelect = false
				})
				
			}
			if(item.num <= 1){
				return
			}
		},
		toItem(){
			uni.switchTab({	
				url:'/pages/item/item'
			})
		},
		toDetail(id){
			uni.navigateTo({
				url:'/pages/index/detail?goodsid='+id
			})
		},
	}
}
